___CSS_LOADER_EXPORT___.push([module.id, `@charset "utf-8";


:root {
    --primary-color: rgba(91, 91, 235, 0.836);
}

* {
    outline: 0;
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
}

body {
    background-color: var(--primary-color);
}

header {
    text-align: center;
    padding: 10px;
    color: white;
    text-shadow: 1px 1px 1px black;
    font-size: 1.3em;
    font-weight: bold;
}

main {
    background-color: white;
    width: 800px;
    margin: auto;
    padding: 10px 20px 10px 20px;
    border-radius: 8px;
    box-shadow: 1px 2px 1px rgba(0, 0, 0, 0.527);
}

main h1 {
    padding: 5px;
}

main p {
    text-align: justify;
    text-indent: 30px;
    line-height: 1.3em;
}

label, input {
    margin: 8px;
}

.checkbox {
    width: 20px;
    height: 20px;
}

#num-char {
    width: 40px;
    text-align: center;
}

input:focus {
    outline: 1px solid var(--primary-color);
}

button {
    padding: 3px;
    height: 30px;
    font-size: 1em;
    font-weight: 700;
    margin-top: 20px;
    margin-left: 40%;
}

button:hover {
    background-color: var(--primary-color);
    color: white;
    text-shadow: 1px 1px 1px black;
    transition: 0.3s;
    cursor: pointer;
}

fieldset {
    padding: 10px;
    margin: 10px 0px 10px 0px;
}

legend {
    font-weight: bold;
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA,gBAAgB;;;AAIhB;IACI,yCAAyC;AAC7C;;AAEA;IACI,UAAU;IACV,WAAW;IACX,YAAY;IACZ,sBAAsB;IACtB,oCAAoC;AACxC;;AAEA;IACI,sCAAsC;AAC1C;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,YAAY;IACZ,8BAA8B;IAC9B,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,uBAAuB;IACvB,YAAY;IACZ,YAAY;IACZ,4BAA4B;IAC5B,kBAAkB;IAClB,4CAA4C;AAChD;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,mBAAmB;IACnB,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,uCAAuC;AAC3C;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,cAAc;IACd,gBAAgB;IAChB,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,sCAAsC;IACtC,YAAY;IACZ,8BAA8B;IAC9B,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,yBAAyB;AAC7B;;AAEA;IACI,iBAAiB;AACrB","sourcesContent":["@charset \"utf-8\";\r\n@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,700;1,400&display=swap');\r\n\r\n\r\n:root {\r\n    --primary-color: rgba(91, 91, 235, 0.836);\r\n}\r\n\r\n* {\r\n    outline: 0;\r\n    margin: 0px;\r\n    padding: 0px;\r\n    box-sizing: border-box;\r\n    font-family: 'Open Sans', sans-serif;\r\n}\r\n\r\nbody {\r\n    background-color: var(--primary-color);\r\n}\r\n\r\nheader {\r\n    text-align: center;\r\n    padding: 10px;\r\n    color: white;\r\n    text-shadow: 1px 1px 1px black;\r\n    font-size: 1.3em;\r\n    font-weight: bold;\r\n}\r\n\r\nmain {\r\n    background-color: white;\r\n    width: 800px;\r\n    margin: auto;\r\n    padding: 10px 20px 10px 20px;\r\n    border-radius: 8px;\r\n    box-shadow: 1px 2px 1px rgba(0, 0, 0, 0.527);\r\n}\r\n\r\nmain h1 {\r\n    padding: 5px;\r\n}\r\n\r\nmain p {\r\n    text-align: justify;\r\n    text-indent: 30px;\r\n    line-height: 1.3em;\r\n}\r\n\r\nlabel, input {\r\n    margin: 8px;\r\n}\r\n\r\n.checkbox {\r\n    width: 20px;\r\n    height: 20px;\r\n}\r\n\r\n#num-char {\r\n    width: 40px;\r\n    text-align: center;\r\n}\r\n\r\ninput:focus {\r\n    outline: 1px solid var(--prim